import { useCallback } from "react";
import html2canvas from "html2canvas";
import ReactGA from "react-ga4";

function useClipboard(captureRef, displayCount, randomChampions) {
  const copyImageToClipboard = useCallback(async () => {
    if (displayCount !== 15) {
      alert(
        "    15  .\n   15 ."
      );
      return;
    }

    try {
      const canvas = await html2canvas(captureRef.current, {
        foreignObjectRendering: true,
        allowTaint: true,
        useCORS: true,
        scale: 1.1,
      });

      const captureWidth = 680;
      const captureHeight = 860;
      const startX = (canvas.width - captureWidth) / 2;
      const startY = (canvas.height - captureHeight) / 2 - 10;

      const croppedCanvas = document.createElement("canvas");
      croppedCanvas.width = captureWidth;
      croppedCanvas.height = captureHeight;
      const ctx = croppedCanvas.getContext("2d");

      ctx.drawImage(
        canvas,
        startX,
        startY,
        captureWidth,
        captureHeight,
        0,
        0,
        captureWidth,
        captureHeight
      );

      const blob = await new Promise((resolve) =>
        croppedCanvas.toBlob(resolve, "image/png")
      );
      await navigator.clipboard.write([
        new ClipboardItem({ "image/png": blob }),
      ]);

      alert(
        "  .\n(ctrl + v) ."
      );
    } catch (error) {
      console.error("    :", error);
      alert("    .");
    }
  }, [displayCount, captureRef]);

  const copyTextToClipboard = useCallback(() => {
    const blueTeam = randomChampions.table1
      .map((champ) => champ.name)
      .join(", ");
    const redTeam = randomChampions.table2
      .map((champ) => champ.name)
      .join(", ");

    const text = ` (${randomChampions.table1.length}): ${blueTeam}\n (${randomChampions.table2.length}): ${redTeam}`;

    navigator.clipboard.writeText(text).then(
      () => {
        alert(
          "  .\n(ctrl + v) ."
        );
      },
      (err) => {
        console.error("    :", err);
        alert("    .");
      }
    );

    ReactGA.event({
      category: "Button",
      action: "Click",
      label: "Copy Text",
    });
  }, [randomChampions]);

  return { copyImageToClipboard, copyTextToClipboard };
}

export default useClipboard;
